.site-header {
    height: 45px;
    -webkit-transition: all .2s;
    transition: all .2s;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3000;
    width: 100%;
    background-color: transparent;

    @media (min-width: 768px) {
        height: 72px;
    }

    .logo {
        display: block;
        margin-top: .8rem;
        width: 6rem;
        z-index: 9000;

        @include respond-above(sm) {
            margin-top: 1.1rem;
            width: rem(149);
        }

        img {
            width: 100%;
            transition: all .2s;
        }
    }

    .mainav {
        @include reset-list;
        display: none;
        @include respond-above(md) {
            display: block;
        }
        &>li {
            display: inline-block;
            position: relative;
            &>a {
                display: block;
                color: #fff;
                position: relative;
                padding: 0 .8rem;

                height: rem(72);
                line-height: rem(72);                
            }

          

            .subnav{
                @include reset-list;
                padding: 0;
                position: absolute;
                left: 0;
                border-radius: 6px;
                width: 240px;
                background-color: #fff;
                top: rem(72);
                height: 0;
                transition: all .2s;
                opacity: 0;
                overflow: hidden;
                li{
                    border-bottom: 1px #eee solid;
                    float: none;
                    &:last-child{
                        border-bottom: none;
                    }
                    padding: 0 1rem;
                    @include hover{
                        background-color: lighten($color-primary,45%);
                    }
                    a{
                        display: block;
                        padding: .6rem 0;
                        font-size: rem(14);
                        color:$color-text-heading;
                        @include hover {
                           color:$color-primary;
                        
        
                        }
                    }
                }
            }

            &.down{              
                @include hover {
                    &>a{
                        color:lighten($color-primary,30%);
                    }
                    .subnav{
                        height:auto;
                        opacity: 1;
                        box-shadow: 3px 3px 5px rgba(0,0,0,.1)
                    }
                    &::after {
                        content: " ";
                        display: block;
                        position: absolute;
                        left: 50%;
                        bottom: 0;
                        height: 0;
                        width: 0;
                        z-index: 100;
                        transition: all .2s;
                       border: .5rem solid #fff;
                       border-left-color: transparent;
                       border-right-color: transparent;
                       border-top-color: transparent;
                       margin-left: -.5rem;
                    }
                   
                }
            }
            

            &.langs{
                color: #fff;
                a{
                    display: inline-block; padding: 0 .3rem;
                }
              
            }
          
        }
    }

    &.fixheader {
        background-color: $color-primary;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        z-index: 8000;

    }
    .searchbox{ 
        display: none;
    }
    &.forSearch{
        height: 250px;
        background-color: $color-accent;
        .searchbox{
            display: block;
            padding-top: 2.5rem;
            border-bottom: 1px #fff solid;
            input{
                width: 100%;
                font-size: rem(30);
                padding: .8rem 1rem;
                background-color: transparent;
                border:none;
                outline: none;
                color:#fff;
            }
            button{
                background-color: transparent;
                border:none;
                color: #fff;
                cursor: pointer;
                padding: .8rem;
                .iconfont{
                    font-size: rem(30);
                }
            }
        }
    }
}






.menu-toggle {
    width: 28px;
    height: 28px;
    position: absolute;
    top: 8px;
    right: 15px;
    cursor: pointer;
    z-index: 9000;
    display: none;
}

@media (min-width:768px) {

    .menu-toggle {
        top: 15px;
    }
}


.menu-toggle.on .one {
    transform: rotate(45deg) translate(3px, 5px);
}

.menu-toggle.on .two {
    opacity: 0;
}

.menu-toggle.on .three {
    transform: rotate(-45deg) translate(6px, -8px);
}

.one,
.two,
.three {
    width: 100%;
    height: 2px;
    background-color: #000;
    margin: 6px auto;
    backface-visibility: hidden;
    -moz-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.overmenu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(255, 238, 0, 0.95);
    display: none;
}

.overmenu ul {
    list-style: none;
    margin: 0 auto;
    padding: rem(80) 0 0;
    max-width: rem(150);

    @include respond-above(sm) {
        max-width: rem(200);
        padding: rem(100) 0 0;
    }
}

.overmenu ul li {
    position: relative;
    transition: all .2s;

    &::after {
        content: " ";
        display: block;
        height: 0;
        width: 100%;
        background-color: #fff;

        transition: all .2s;
        position: absolute;
        left: 0;
        bottom: .8rem;
        z-index: -1;
    }

    &:hover {
        color: $color-text-heading !important;
        transform: scale(1.1);

        &::after {
            height: 30%;
        }
    }

    a {
        padding: 10px 15px;
        display: block;
        color: #333;
        text-align: center;
        position: relative;
        font-size: rem(24);
        font-weight: lighter;

        @include respond-above(sm) {
            font-size: 2rem;
        }


    }

}



.overmenu ul li a.active {
    color: #000;
    color: $color-text-heading !important;

    &::after {
        content: " ";
        display: block;
        height: .2rem;
        width: 100%;
        background-color: #333;
        margin-top: -13px;
        transition: all .2s;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: -1;
    }

}
